import { createFeatureSelector, createSelector } from '@ngrx/store';
import { RX_DOCUMENT_DESIGNER } from '../../document-designer.constant';
import { IDocumentDesignerModel, IDocumentDesignerState } from './document-designer.types';

export const documentDesignerStateSelector = createFeatureSelector<IDocumentDesignerState>(
  RX_DOCUMENT_DESIGNER.featureSelector
);

export const documentDesignerModelSelector = createSelector(
  documentDesignerStateSelector,
  (documentDesignerState: IDocumentDesignerState) => documentDesignerState.model
);

export const bundleIdSelector = createSelector(
  documentDesignerModelSelector,
  (documentDesignerModel: IDocumentDesignerModel) => documentDesignerModel.bundleId
);

export const definitionNameSelector = createSelector(
  documentDesignerModelSelector,
  (documentDesignerModel: IDocumentDesignerModel) => documentDesignerModel.definitionName
);

export const isDesignModeSelector = createSelector(
  documentDesignerModelSelector,
  (documentDesignerModel: IDocumentDesignerModel) => documentDesignerModel.isDesignMode
);

export const definitionModelFromDefinitionSelector = createSelector(
  documentDesignerModelSelector,
  (documentDesignerModel: IDocumentDesignerModel) => documentDesignerModel.definitionModelFromDefinition
);

export const definitionModelSelector = createSelector(
  documentDesignerModelSelector,
  (documentDesignerModel: IDocumentDesignerModel) => documentDesignerModel.definitionModel
);

export const isDirtySelector = createSelector(
  documentDesignerModelSelector,
  (documentDesignerModel: IDocumentDesignerModel) => documentDesignerModel.isDirty
);

export const savedDefinitionNameSelector = createSelector(
  documentDesignerModelSelector,
  (documentDesignerModel: IDocumentDesignerModel) => documentDesignerModel.savedDefinitionName
);

export const originalDefinitionSelector = createSelector(
  documentDesignerModelSelector,
  (documentDesignerModel: IDocumentDesignerModel) => documentDesignerModel.originalDefinition
);
